.content {
  font-family: Raleway;
  display: flex;
  margin-top: 6.7em;
  flex-direction: column;
}
@media only screen and (max-width: 880px) {
  .content {
    margin-top: 2em;
  }
}
.content .tabsWrap {
  display: flex;
  max-width: 546px;
}
.content .tab {
  width: 100%;
  border: 1px solid #a8899d;
  padding: 0.95em 0;
  color: #a8899d;
  background: #ffffff;
  box-shadow: 2px 4px 5px rgba(37, 48, 149, 0.1);
  border-radius: 5px;
  cursor: pointer;
}
@media only screen and (max-width: 560px) {
  .content .tab {
    font-size: 0.8em;
    padding: 0.7em 0;
  }
}
.content .tab:hover:is(.active) {
  background-color: #bb5285;
}
.content .tab:hover:not(:disabled):not(.active) {
  color: #482c42;
  background: #ffffff;
}
.content .active {
  background-color: #bb5285;
  color: #ffffff;
  cursor: auto;
  border: none;
}
.content .active:hover {
  color: #ffffff;
}
.content .description {
  width: 100%;
  background-color: #ffffff;
  border-radius: 5px;
  padding: 3.5em;
  display: flex;
  grid-column-gap: 2em;
  justify-content: space-between;
  box-sizing: border-box;
}
@media only screen and (max-width: 880px) {
  .content .description {
    flex-direction: column;
    grid-row-gap: 1em;
  }
}
@media only screen and (max-width: 560px) {
  .content .description {
    padding: 2em 1.5em;
  }
}
.content .description .listWrap {
  display: flex;
  flex-direction: column;
}
.content .description .tabTitle {
  font-size: 2em;
  font-weight: 600;
  color: #482c42;
}
.content .description .list {
  display: flex;
  flex-direction: column;
  font-size: 1em;
  font-weight: 400;
  color: #865e7e;
  row-gap: 1.4em;
  padding-top: 2em;
  padding-left: 1.5em;
}
@media only screen and (max-width: 880px) {
  .content .description .list {
    font-size: 0.9em;
  }
}
.content .description .list li {
  list-style-image: url("/images/checkmark.svg");
  padding-left: 1em;
}
.content .description .button {
  max-width: 260px;
  margin: 3.5em 0 0;
}
@media only screen and (max-width: 560px) {
  .content .description .button {
    max-width: 100%;
    margin: 1em 0 0;
  }
}
.content .img {
  max-width: 458px;
}
.content .image {
  display: flex;
  width: 100%;
  min-width: 458px;
  max-width: 458px;
  height: 310px;
  max-height: 310px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
@media only screen and (max-width: 880px) {
  .content .image {
    height: 271px;
    min-width: 0;
    max-width: 401px;
  }
}
@media only screen and (max-width: 560px) {
  .content .image {
    height: 188px;
    min-width: 0;
  }
}